
@import url('https://fonts.cdnfonts.com/css/anton');
@import url('https://fonts.cdnfonts.com/css/crimson-pro');
@import url('https://fonts.cdnfonts.com/css/gloock-2');

.journal-two-landing-headline {
    text-align: center;
    width: 60%;
    margin: 0 auto;
    text-transform: uppercase;
    margin-top: 0px;
}

.journal-two-landing-headline h1 {
    font-family: 'Anton', sans-serif;
    font-size: 130px;
    /* line-height: 10rem; */
}

.journal-two-landing-headline h1:nth-of-type(2) {
    margin-top: -50px;
}

.journal-two-landing-headline::before,
.journal-two-landing-headline::after {
    content: "";
    position: absolute;
    width: 60%;
    height: 2px;
    background-color: #353535;
    left: 50%;
    transform: translateX(33%);
}

.journal-two-landing-headline::before {
    height: 8px;
    left: 0;
    margin-top: 5px;
}

.journal-two-landing-headline::after {
    height: 1.5px;
    left: 0;
}

.journal-two-landing-content {
    margin-top: 20px;
    margin-bottom: 30px; 
}

.journal-two-landing-content::after {
    content: "";
    position: absolute;
    width: 60%;
    height: 2px;
    background-color: #353535;
    left: 50%;
    transform: translateX(33%);
}

.journal-two-landing-content::after {
    height: 3px;
    left: 0;
    margin-top: 25px;
}

.journal-two-landing-content h5 {
    display: flex;
    justify-content: center;
    font-family: 'Gloock', sans-serif;
    margin-left: -2rem; 
    font-size: 30px;
}

.jtl-content-columns {
    display: flex;
    justify-content: center;
    margin-top: 10px;
}

.jtl-content-columns p {
    width: 40%;
    font-family: 'Crimson Pro', sans-serif;
    text-align: justify;
    padding-right: 20px;
}

.jtl-content-columns img {
    width: 20%;
    max-height: 300px;
    object-fit: cover;
    border-radius: 10px;
    margin-right: 5px;
}

/* responsiveness for small devices like phones */

@media screen and (max-width: 600px) {

    .journal-two-landing-headline::before {
        margin-top: -15px;
    }
    
    .journal-two-landing-headline {
        margin-top: 10px;
    }

    .journal-two-landing-headline h1 {
        font-size: 50px;
        line-height: 4rem;
    }

    .journal-two-landing-headline h1:nth-of-type(2) {
        margin-top: 0px;
    }

    .journal-two-landing-content {
        margin-bottom: 40px;
    }

    .journal-two-landing-content::after {
        margin-top: 1.1rem;
    }

    .journal-two-landing-content h5 {
        width: 60%;
        text-align: justify;
        margin: 0 auto;
        font-size: 17px;
    }

    .jtl-content-columns {
        flex-direction: column;
        align-items: center;
    }

    .jtl-content-columns p {
        width: 60%;
        padding-right: 0;
        margin-bottom: 20px;
    }

    .jtl-content-columns img {
        width: 60%;
    }
}

/* responsiveness for medium devices like tablets */

@media screen and (min-width: 768px) and (max-width: 1024px) {

    .journal-two-landing-headline h1 {
        font-size: 100px;
    }

    .journal-two-landing-headline h1:nth-of-type(2) {
        margin-top: -40px;
    }

    .journal-two-landing-content {
        margin-bottom: 45px;
    }

    .journal-two-landing-content h5 {
        width: 60%;
        text-align: center;
        margin: 0 auto;
    }

    .jtl-content-columns img {
        max-height: 400px;
    }
}