@import url('https://fonts.cdnfonts.com/css/dm-serif-text');

.journal-two-section {
    font-family: 'Times New Roman', Times, serif;
    background-color: #f9f9f9;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.journal-container img {
    margin: 0 auto;
    max-height: 200px;
    width: 80%;
    object-fit: cover;
}

.jt-content {
    margin-top: 20px;
}

.jt-content p {
    margin-bottom: 15px;
    text-align: justify;
}
