.journal-four-section {
    font-family: 'Times New Roman', Times, serif;
    background-color: #f9f9f9;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.jf-content-columns {
    display: flex;
    align-items: flex-start;
    margin-top: 20px;
    margin-bottom: 20px;
}

.jf-content-columns img {
    width: 40%;
    min-height: 350px;
    margin-right: 20px;
    object-fit: cover;
    border-radius: 5px;
    /* object-position: top; */ 
}

.jf-content-columns p {
    width: 60%;
    margin: 0;
    text-align: justify;
}

.jf-content {
    margin-top: 20px;
}

.jf-content p {
    margin-bottom: 15px;
    text-align: justify;
}

@media screen and (max-width: 600px) {
    .jf-content-columns {
        display: grid;
        grid-template-columns: 1fr;
        margin-bottom: 0;
    }

    .jf-content-columns img {
        width: 100%;
        margin-bottom: 10px;
    }

    .jf-content-columns p {
        width: 100%;
    }
}